*
{
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
    /* border: 1px solid red; */
      
}

/* ----------------------------- Root--------------------- */

:root
{
   --bg-color: #1f242d;
   --second-bg-color : #323946;
   --text-color : #fff; 
   --main-color : #0ef;
}



/*----------------------- Home Styling ------------------ */

.Home
{

    color: white;
    background-color: rgb(44, 44, 46);
   
    width: 100%;
    height: 600px;
    display: grid;
    grid-template-columns: 55% 45%;
   
  
}
.Home div
{
    margin: 0px 40px;
        
    
}
.Homeleft
{
   
/* border: 2px solid yellow; */
   padding-top: 40px;
    height: 500px;
    display: grid;
    grid-template-rows: 12.5%,12.5%,17%,25%,19%,14%;
}

.HomeIcons
{
    display: flex;
    column-gap: 30px;
}
.HomeIcons > p
{
    width: 2.5rem;
    height: 2.5rem;
   border: 2px solid rgb(0, 204, 255);
    color:  rgb(0, 204, 255);
   border-radius: 50%;
   display: flex;
   justify-content: center;
   align-items: center;
}
.HomeIcons >  p:hover
{
    background: var(--main-color);
    color: var(--second-bg-color);
    box-shadow: 0 0 1rem var(--main-color);
    cursor: pointer;
}
.Homeright
{
    /* border: 2px solid red;  */
    /* border: 1px solid red; */
  
    display: flex;
    justify-content: center;
    align-items: center;
}

@keyframes floatImage {
    0%{
       transform: translateY(0);
    }
    50%{
     transform: translateY(-2.4rem);
   }
  100%{
     transform: translateY(0);
  }
 }

.HomeImg img
{

    color: black;
    border-radius: 50%;
    width: 550px;
    height: 500px;
   border: 3px solid rgb(124, 201, 220);

 
   box-shadow: 0 0 2.5rem rgb(0, 204, 255);
   
   display: flex;
   justify-content: center;
   align-items: center;
   animation: floatImage 4s ease-in-out infinite;
}

.Homebtn button
{
    display: inline-block;
    background: var(--main-color);
    border-radius: 4rem;
    box-shadow: 0 0 1rem var(--main-color); 
    font-size: 16px;
    color : var(--second-bg-color);
    letter-spacing: .1rem;
    font-weight: 600;
    transition: .5s ease;
    height:  45px;
    width: 160px;
    animation: HomeAnimation 1.4s infinite alternate;
}
@keyframes  HomeAnimation{
     0%
     {
        transform: scale(1.1);
     } 
     100%
     {
        transform: scale(1.3);
     }
}


.Homebtn button:hover{
    box-shadow: none;
    cursor: pointer;
}




/* ------------------------ Home Responsivness ------------------------------------------- */
@media screen and (max-width: 1025px){
    .Home
    {
        height: auto;
        display: flex;
        flex-wrap: wrap;
    }
    
}
@media screen and (max-width: 769px){
    .HomeImg img
    {
        margin-top: 40px;
        height: 400px;
        width: 400px;
    }
}


@media screen and (max-width: 426px){
    .Home
    {
        height: 1150px;
    }
    .Home > div
    {
        margin: 10px 0px;
    }
    .HomeImg img {
        margin-top: 60px;
        width: 320px; 
        height: 320px;
    }
    .Homeleft > div 
    {
        padding-top:  30px;
    }
}  

@media screen and (max-width: 376px){
    .HomeImg img {
        width: 270px; 
        height: 270px;
    }
}  
@media screen and (max-width: 321px){
  .Homeleft
  {
     width: 97%;
  }
  .Homeright{
    margin-top: 110px;
  }
    .HomeImg img {
        width: 220px; 
        height: 220px;
    }
} 




/* --------------------About Section -----------------*/
.About
