/* ------------------------ Footer Responsiveness -------------------------- */

@media screen and (max-width: 1045px) {
  .Footer {
    display: flex;
    flex-wrap: wrap;
    height: auto;
    row-gap: 30px;
  }
}

@media screen and (max-width: 769px) {
  .Footer {
    padding-left: 50px;
  }

  .Footerright {
    display: flex;
    flex-wrap: wrap;
  }
}

@media screen and (max-width: 426px) {
  .Footer {
    width: 100%;
    padding: 20px;
    box-sizing: border-box;
  }

  .Footerleft {
    margin-left: 0;
    text-align: center;
    order: 1;
    width: 100%;
  }

  .Footermid {
    padding-left: 0;
    text-align: center;
    order: 2;
    width: 100%;
  }

  .Footerright {
    order: 3;
    width: 100%;
    justify-content: center;
    margin-top: 20px;
  }

  .Footerright > div {
    margin-right: 10px;
  }
}

@media screen and (max-width: 376px) {
  .Footer {
    padding: 10px;
  }
}

@media screen and (max-width: 321px) {
  .Footer {
    padding: 5px;
  }
}