*
{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    /* border: 1px solid red; */
}



/* ----------------------------- Root--------------------- */
:root {
    --bg-color: #1f242d;
    --second-bg-color: #323946;
    --text-color: #fff;
    --main-color: #0ef;
}

/* -------------------- NavBar Styling ------------------ */

.Navbar {
    width: 100%;
    height: 60px;
    background-color: #323946;
    color: white;
    /*
    display: flex;
    justify-content: space-between; /* Adjust alignment */
  /*  align-items: center; /* Vertically center items */
   /* padding: 0 30px; /* Add some padding */
    display: grid;
    grid-template-columns: 80% 20%;
}
.Navbar a
{
    font-size: 18px;
    text-decoration: none;
    color: white;
}
.Navbar a:hover
{   
    color: #0ef;
}
.Navleft
{
    display: grid;
    grid-template-columns: 25% 75%;
}

.Portfolio  {
   
    display: flex;
    align-items: center;
    justify-content: center;
}

.Navmid, .Navright {
    display: flex;
    align-items: center; /* Vertically center items */
    justify-content: center;
}

.Navmid a
{
    padding: 0px 30px;
}
.Navmid a, .Navright button {
    margin-right: 20px; /* Adjust spacing between items */
}

.NavIcon {
    display: none;
    font-size: 20px;
    color: white;
}

.btn button
{
    background: var(--main-color);
    height: 30px;
    width: 100px;
    border-radius: 4rem;
    box-shadow: 0 0 1rem var(--main-color); 
    color : var(--second-bg-color);
    animation: scaleAnimation 1.5s infinite alternate;
    text-decoration: none;
}
@keyframes scaleAnimation {
    0% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1.3);
    }
  }

.btn button:hover
{
    cursor: pointer;
    box-shadow: none;
    color : var(--second-bg-color);

}

/* Media Queries */
@media only screen and (max-width: 1024px) {
    .Navmid a, .Navright button {
        margin-right: 10px; /* Reduce spacing between items */
    }
}

@media only screen and (max-width: 768px) {
    .Navmid, .btn{
        display: none; /* Hide middle and right sections */
    }


    .NavIcon {
        display: block; /* Show menu icon */
    }
    
    
.NavIcon {
            display: flex; /* Show menu icon */
            align-items: center; /* Center icon vertically */
    }
    
        .NavIcon svg {
            font-size: 24px; /* Adjust icon size */
        }
    }


.NavIcon
{
    margin-left: 10px;
}


/* Adjustments for smaller screen sizes */
@media only screen and (max-width: 425px) {
    .Navbar {
        height: 50px; /* Adjust height for smaller screens */
    }

    .Navmid a, .Navright button {
        margin-right: 10px; /* Further reduce spacing */
    }
    .Portfolio
    {
        padding-left: 40px;
    }
}

@media only screen and (max-width: 375px) {
    .Navmid a, .Navright button {
        margin-right: 5px; /* Further reduce spacing */
    }
}


/*----------------------Navlink styling -----------*/

 .Nav {
    width: 100%;
    display: flex;
    flex-wrap: wrap; /* Allow items to wrap */
    justify-content: center; /* Center items horizontally */
    background-color: var(--second-bg-color); /* Use variable for background color */
    /* Add padding */
}

.Nav a {
    text-decoration: none;
    color: var(--text-color); /* Use variable for text color */
    padding: 10px 20px;
    margin: 5px; /* Add margin between links */
    border-radius: 5px; /* Add border-radius for better appearance */
}

.Nav a:hover {
    background-color: var(--main-color); /* Change background color on hover */
    color: var(--second-bg-color); /* Change text color on hover */
}

@media screen and (max-width: 768px) {
    .Nav {
        flex-direction: column; /* Stack items vertically on smaller screens */
        align-items: center; /* Center items horizontally */
    }
}

@media screen and (max-width: 425px) {
    .Nav a {
        padding: 10px; /* Adjust padding for smaller screens */
    }
}